            </div>
          </b-col>
        </b-row>
        <b-row class="mb-1">
          <b-col cols="3"></b-col>
          <b-col>
            <div class="input-group">
              <b-form-textarea
                id="actor_info"
                v-model="createActor.actorInfo"
                rows="3"
                max-rows="10"
              ></b-form-textarea>
            </div>
          </b-col>
        </b-row>
      </b-container>

      <template #modal-footer>
        <b-button
          variant="secondary"
          size="sm"
          class="float-right"
          @click="isCreateActorModal = false"
