    <!----------- -------------->
    <b-modal v-model="isGroupModal" title="">
      <b-container fluid>
        <b-row class="mb-1">
          <b-col cols="3"></b-col>
          <b-col>
            <div class="input-group">
              <input
                class="form-control"
                placeholder=""
                type="text"
                name="create_group_name"
                v-model="createGroup.groupName"
                v-bind:class="[createGroup.groupNameValid]"
              />
            </div>
          </b-col>
        </b-row>
        <b-row class="mb-1">
          <b-col cols="3"></b-col>
          <b-col>
            <div class="input-group">
              <select
                class="form-control"
                v-model="createGroup.groupColor"
                v-bind:class="[createGroup.groupColorValid]"
              >
                <option :value="null" disabled>
                  
                </option>
                <option
                  v-for="(val, key) in commonMstColor"
                  :key="key"
                  v-bind:value="val.common_value"
                >
                  {{ val.common_info }}
                </option>
              </select>
              <b-button variant="info" @click="createGroupApi()">
                
              </b-button>
            </div>
          </b-col>
        </b-row>
        <b-row class="mb-1">
          <table class="table">
            <thead class="thead-light">
              <tr>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(row, key) in groupList"
                :key="key"
                v-bind:style="{
                  backgroundColor: row.group_color,
                }"
              >
                <td>{{ row.group_name }}</td>
                <td>
                  <button
                    type="button"
                    class="btn btn-success"
                    @click="
                      isEditGroupModalOpen(
                        row.group_id,
                        row.group_name,
                        row.group_color,
                        row.version
                      )
                    "
                  >
                    <font-awesome-icon icon="pencil-alt" />
                  </button>
                </td>
                <td>
                  <button
                    type="button"
                    class="btn btn-danger"
                    @click="isDeleteGroupModalOpen(row.group_id)"
                  >
                    <font-awesome-icon icon="times" />
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </b-row>
      </b-container>

      <template #modal-footer>
        <b-button
          variant="secondary"
          size="sm"
          class="float-right"
          @click="isGroupModal = false"
        >
          
        </b-button>
      </template>
    </b-modal>
    <!----------- -------------->
