        </b-row>
        <b-row class="mb-1">
          <b-col cols="3"></b-col>
          <b-col>
            <div class="input-group">
              <select
                class="form-control"
                v-model="editGroup.groupColor"
                v-bind:class="[editGroup.groupColorValid]"
              >
                <option
                  v-for="(val, key) in commonMstColor"
                  :key="key"
                  v-bind:value="val.common_value"
                >
                  {{ val.common_info }}
                </option>
